import Splide from '@splidejs/splide';


if (document.querySelector('#carousel')) {
  new Splide( '#carousel', {
    pagination: false,
    type: 'slide',
    perPage: 4,
    perMove: 1,
    gap: 32,
    breakpoints: {
      500: {
        perPage: 1,
        gap: 10,
      },
      767: {
        perPage: 2,
        gap: 10,
      },
      1024: {
        perPage: 2,
        gap: 16,
      },
      1400: {
        perPage: 3,
        gap: 32,
      },
    }
  } ).mount();
}
